.item {
  padding: 10px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.trash {
  color: #8db55f;
  font-size: 25px;
  position: absolute;
  top: 15px;
  left: 15px;
  cursor: pointer;
}
.item img {
  width: 50%;
  border-radius: 10px;
}
.item-info {
  flex-basis: 45%;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: center;
}
.item-info h3,
.item-info span {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
  text-align: center;
}
.item-form {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 10px;
}
.item-form button {
  border: none;
  outline: none;
  background-color: transparent;
  color: #8db55f;
  font-size: 20px;
  cursor: pointer;
}
.item-form input {
  margin-bottom: 0;
  width: 40px;
  text-align: center;
}
